.skills {
  background: rgb(26, 255, 140);
  background: linear-gradient(
    90deg,
    rgba(26, 255, 140, 1) 0%,
    rgba(236, 255, 0, 1) 100%
  );
  min-height: var(--skills-contact-height);
  padding: var(--spacing-lg) 0;
  text-align: center;
}

.skills-container {
  margin: 0 auto;
  width: 90%;
}

.skills-heading {
  color: var(--white);
}

.skill-card-container {
  justify-content: space-between;
  margin: 0 auto;
  width: 100%;
}

.skill-card-animation-wrapper {
  width: 95%;
}

.skill {
  font-style: italic;
  text-decoration: 2px underline var(--action-color);
}

@media only screen and (min-width: 768px) {
  .skill-card-container {
    display: flex;
    width: 100%;
    margin: 0 auto;
  }

  .skill-card-animation-wrapper {
    width: 31%;
  }

  .skill-card {
    width: 95%;
  }
}

@media only screen and (min-width: 1200px) {
}
